* {
  box-sizing: border-box;
}
.page {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.section {
  box-sizing: border-box;
  width: 100%;
  padding: 40px 200px;
  /* margin: 40px 250px; */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  background-color: black;
  color: white;
  border: none;
  padding: 12px 24px;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: bold;
  letter-spacing: 2px;
}

.button:hover {
  background-color: grey;
}

h4 {
  font-size: 14px;
  margin: 0 0 24px 0;
  text-transform: uppercase;
  letter-spacing: 5px;
}

h3 {
  margin: 0 0 16px 0;
}

.image {
  background-color: #cccccc;
  width: 120px;
  height: 140px;
}

.largeImage {
  background-color: #cccccc;
  width: 100%;
  height: 200px;
}

@media screen and (max-width: 820px) {
  .section {
    margin: 0;
    padding: 0 25px;
  }
}
