.clientsWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #add8e6;
  padding: 25px;
  /* padding: 45px 25px; */
}

.clientsImageWrapper {
  display: flex;
